import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue"
import ygcComment from '@/components/ygc-comment/ygc-comment.vue';
import uParse from '@/components/gaoyia-parse/parse.vue'
export default{
	components:{
		uniLoadMore,
		ygcComment,
		uParse
	},
	data(){
		return{
			id:'',
			active:1,
			tabActive:0,
			sh:'',
			sh2:'',
			dataList:[],
			commentList:[],
			data:'',
			like:'',
			comment:'',
			repost:'',
			isLike:false,
			page:1,
			pageSize:10,
			total:0,
			more:'',
			articleH:'',
			windowH:'',
			topH:'',
			readComplete:false,
			uid:'',
			token:'',
			logined:''
		}
	},
	onLoad(e){
		this.id=e.id
		this.like=e.like
		this.comment=e.comment
		this.repost=e.repost
		var userInfo=uni.getStorageSync('userInfo')
		if(userInfo!='' & userInfo!=null & userInfo!=undefined){
			this.logined=true
			this.uid=userInfo.uid
			this.token=userInfo.token
		}else{
			this.logined=false
		}
		this.getDetail()
		uni.$on('logined',function(){
			var userInfo2=uni.getStorageSync('userInfo')
			that.logined=true
			that.uid=userInfo2.uid
			that.token=userInfo2.token
			that.getDetail()
		})
	},
	onPageScroll(e){
		// this.calcArticleHeight()
		if(!this.readComplete){
			if(e.scrollTop>=this.articleH-(this.windowH-this.topH)){
				this.readComplete=true
				this.$msg('阅读完成')
			}
		}
	},
	computed: {
	     noMore () {
	       return this.commentList.length >= this.total
	     },
	   },
	   mounted(){
	   	// var that=this
		// uni.$on('updated',function(){
		// 	that.calcArticleHeight()
		// })
	   	// this.getDetail()
	   	// setTimeout(function(){
	   	// 	that.$getHeight('#sv',(res) =>{
	   	// 		that.sh=res
	   	// 	})
	   	// },0)
	   },
	methods:{
		toggleTab(t){
			this.tabActive=t
		},
		async getDetail(){
			var that=this
			 await that.getList()
			var params={
			  id:this.id
			}
			  var url='&r=api.college.hotarticle.detail&id='+this.id
			  this.$apiPost(url,params).then((res) =>{
				  that.data=res.data
				that.$nextTick(function(){
				 						 that.$loading()
				 	setTimeout(function(){
				 							that.calcArticleHeight()
				 						},100)
				 })
			  })
		},
		getList(p){
			var that=this
			// var params={
			//   page:p,
			//   pagesize: this.pageSize
			// }
			  var url='&r=api.college.hotarticle.comment&page='+this.page+'&pagesize='+this.pageSize+'&collegeid='+this.id
			  this.$apiPost(url).then((res) =>{
				  that.total=res.total
				  that.commentList=that.dataList.concat(res.data)
				  that.more=''
				 // if(that.page==1){
					//  that.$nextTick(function(){
					//  						 that.$loading()
					//  	setTimeout(function(){
					//  							that.calcArticleHeight()
					//  						},100)
					//  })
				 // }
			  }).catch((reason) =>{
				 // if(that.page==1){
					//  that.$nextTick(function(){
					//  						  that.$loading()
					//  	setTimeout(function(){
					//  		that.calcArticleHeight()
					//  	},1000)
					//  })
				 // }
			  })
			  
		},
		calcArticleHeight(){
			var that=this
			uni.getSystemInfo({
			  	success: (resu) => {
					that.windowH=resu.windowHeight
			  	const query = uni.createSelectorQuery()
			var  qs=query.select('#article').boundingClientRect()
			  	query.exec(function(res) {
					console.log(res)
					that.articleH=res[0].height
					that.topH=res[0].top
					if(this.articleH-(this.windowH-this.topH)<=0){
						that.readComplete=true
						that.$msg('篇幅小，已阅读完成')
					}
					uni.hideLoading()
			  		})
			  	}
			 })
		},
		toBottom(){
			if(this.noMore){
				this.more='noMore'
				return;
			}
			var that=this
			this.more='loading'
		  // setTimeout(function(){
			  that.page++
			  that.getList(that.page)
		  // },2000)
		},
		pubComment(e){
			var ce=this.$operateInterceptor(this.logined)
			if(!ce){
				return ;
			}
			var that=this,url
				url='&r=api.college.hotarticle.commentAdd'
			var params={
			  collegeid:this.data.id,
			  uid:this.uid,
			  token:this.token,
			  content:e
			}
			  this.$apiPost(url,params).then((res) =>{
				  that.$msg('发布评论成功')
				  that.showComment()
				  this.$refs.ygcComment.maskState=0
				  this.$refs.ygcComment.content=''
				  that.$forceUpdate()
			  })
		},
		showComment(){
			this.page=1
			this.total=0
			this.more=''
			this.commentList=[]
			this.getList()
		},
		openComment(){
			this.$refs.ygcComment.maskState=1
		},
		toShop(id){
			uni.navigateTo({
				url:`/pages/bussiness/shopPreview?id=${id}`
			})
		},
		toggleZan(zaned){
			var ce=this.$operateInterceptor(this.logined)
			if(!ce){
				return ;
			}
			var that=this,url
			if(!zaned){
				url='&r=api.college.hotarticle.doLike'
			}else{
				url='&r=api.college.hotarticle.doLikeCancel'
			}
			var params={
			  collegeid:this.data.id,
			  uid:this.uid,
			  token:this.token
			}
			  this.$apiPost(url,params).then((res) =>{
				  that.data.isLike=!that.data.isLike
				  that.$forceUpdate()
			  })
		}
	}
}
